import React from "react";
import Card from "../components/ComplaintCard";

const Cards = [
  [
    <Card name="Ahsan Iqbal" imageSrc="https://joeschmoe.io/api/v1/random" />,
    <Card name="Hammad Qayyun" imageSrc="https://joeschmoe.io/api/v1/jon" />,
  ],
  [
    <Card name="Ahsan Iqbal" imageSrc="https://joeschmoe.io/api/v1/2" />,
    <Card name="Hammad Qayyun" imageSrc="https://joeschmoe.io/api/v1/4" />,
  ],
];
function Complaints() {
  return (
    <>
      {Cards.map((item, index) => {
        return (
          <div
            style={{ marginInline: 300, display: "flex", flexDirection: "row" }}
          >
            {item[0]}
            {item[1]}
          </div>
        );
      })}
    </>
  );
}

export default Complaints;
